<template>
    <div>
        <introduce-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <!--<insert-card v-if="currentStep === steps.findIndex(step=>step.key === 'insertCardFirst')"></insert-card>-->
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
            wait-text="请在客户端处理"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
// import insertCard from './components/insert-card';
import introduceInfo from './components/introduce-info';
import process from '@/components/process';
import { logout } from '@/utils';
import { wsSend } from '@/utils/websocket';
import { hxAppear } from '@/service/common';
import { isEqual } from 'lodash';

export default {
    name: 'ClientHXIndex',
    components: {
        introduceInfo,
        process
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera',
            identifyCard: 'identifyCard'
        }),
        ...mapState('permanent', {
            HXWebSocket: 'HXWebSocket',
            lightWebSocket: 'lightWebSocket',
            manufacturerType: 'manufacturerType',
            BUSINESS_EN_NAME_CODE: 'BUSINESS_EN_NAME_CODE'
        }),
        ...mapState('entryAndExit', {
            personInfo: 'personInfo',
            entryAndExitApplyInfo: 'entryAndExitApplyInfo'
        })
    },
    data () {
        return {
            imgUrl: '',
            intervalId: ''
        };
    },
    mounted () {
        let steps = [];
        if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.exitEntryApply) {
            steps = [
                {
                    name: '业务须知',
                    isActive: true,
                    visible: true,
                    key: 'serverInfo'
                },
                {
                    name: '客户端处理',
                    isActive: false,
                    visible: true,
                    key: 'process'
                }
            ];
        } else {
            steps = [
                {
                    name: '客户端处理',
                    isActive: false,
                    visible: true,
                    key: 'process'
                }
            ];
        }
        this.setSteps(steps);
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        handleBusiness () {
            wsSend(this.lightWebSocket, 'lightCtrl@B3:ON');
            this.clientHandle();
            this.intervalId = setInterval(async () => {
                this.clientQuery();
            }, 1000);
        },
        // 航信客户端特殊处理
        async clientHandle () {
            const data = {
                ACTION: 'callFuncs',
                DATA: { FunName: 'BtnVisa' }
            };
            if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.exitEntryApply) {
                data.DATA.FunName = 'BtnVisa';
            } else if (this.businessTypeCode === this.BUSINESS_EN_NAME_CODE.exitEntryCheck) {
                data.DATA.FunName = 'BtnRead';
            }

            const res = await hxAppear({
                json: JSON.stringify(data)
            });
            if (res && res.RESULT === 0) {
                console.log('航信客户端开启成功');
            } else if (res) {
                this.changeTipPage({ message: '出现了点意外，请联系管理员！' });
            }
        },
        async clientQuery () {
            // 航信客户端特殊处理
            const res = await hxAppear({
                json: JSON.stringify({
                    ACTION: 'queryDisplay',
                    DATA: { isDisplay: 'false' }
                })
            });
            if (res && res.RESULT === 0) {
                if (res && isEqual(res.DATA, {})) {
                    // 已经隐藏
                    logout();
                }
                if (res.DATA && res.DATA.DISPLAYWINDOW === 'F_Main_Choose') {
                    // 回到主页
                    this.clientHidden();
                }
                if (res.DATA && res.DATA.DISPLAYWINDOW === '') {
                    // 正在操作页面
                }
            }
        },
        async clientHidden () {
            // 航信客户端特殊处理

            await hxAppear({
                json: JSON.stringify({
                    ACTION: 'setDisplay',
                    DATA: { isDisplay: 'false' }
                })
            });

            logout();
        }
    },
    beforeDestroy () {
        wsSend(this.lightWebSocket, 'lightCtrl@B3:OFF');
        clearInterval(this.intervalId);
    }
};
</script>
