<template>
    <div>
        <div class="kd-page-title">业务须知</div>
        <div class="kd-page-content-wrapper wrap-info">
            <div class="serverinfo-content">
                <div class="introduce-info">
                    <div
                        class="text-info"
                        :style="{top:changeTop+'px'}"
                        :class="onePage ? 'onePageTextInfo' : ''"
                        v-loading="loading"
                    >
                        <div ref="textInfo" v-html="text"></div>
                    </div>
                    <div
                        class="change-page"
                        v-if="!onePage && !loading"
                    >
                        <el-button
                            :disabled="downDisable"
                            @click="changePage('down')"
                        >
                            上&nbsp;&nbsp;&nbsp;&nbsp;翻
                        </el-button>
                        <el-button
                            :disabled="upDisable"
                            @click="changePage('up')"
                        >
                            下&nbsp;&nbsp;&nbsp;&nbsp;翻
                        </el-button>
                    </div>
                </div>
                <div
                    class="checked-info"
                    v-if="!loading"
                >
                    <el-checkbox-group v-model="checked">
                        <el-checkbox label="阅读并同意">
                            阅读并同意
                        </el-checkbox>
                    </el-checkbox-group>
                </div>
            </div>
            <img class="policeImg" :src="animalPic">
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-zhongzhi"
                text="返回"
                :button-handle="backMenu"
            />
            <kd-button
                v-if="!loading"
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!checked"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { getUserNotice } from '@/service/common';
import { changeRouter } from '@/utils';
import audioCar from '@/assets/media/trafic/8.mp3';
import { playAudio } from '@/utils/media';
import { getServerInfoPic } from '@/utils/regional';

export default {
    name: 'ServerInfo',
    props: {
        onePage: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        })
    },
    data () {
        return {
            loading: false,
            checked: false,
            downDisable: true,
            upDisable: false,
            changeTop: 0,
            limitTop: '',
            text: '',
            animalPic: getServerInfoPic(this.CONSTANT.location)
        };
    },
    async mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.loading = true;
        const res = await getUserNotice({
            type: this.businessTypeCode
        });
        this.loading = false;
        if (res && res.code === '0') {
            this.text = res.result.content;
        } else if (res) {
            this.changeTipPage({ message: res.message });
        }
        this.$nextTick(() => {
            this.limitTop = 0 - this.$refs.textInfo.offsetHeight;
        });
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    watch: {
        changeTop: {
            handler (newValue, oldValue) {
                this.limitTop = 0 - this.$refs.textInfo.offsetHeight;
                this.upDisable = !(newValue > this.limitTop);
                this.downDisable = !(newValue < 0);
            },
            deep: true
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'changeVisible']),
        backMenu () {
            this.changeVisible({
                visibleSteps: false,
                visibleTip: false,
                visibleBusinessName: false,
                visibleJurisdiction: false
            });
            changeRouter('/');
        },
        nextStep () { // 下一步
            if (!this.checked) {
                return;
            }
            this.changeStep('down');
        },
        preStep () { // 上一步
            this.changeStep('up');
        },
        changePage (type) { // 翻页
            if (type === 'up') {
                this.changeTop -= 40;
            } else {
                this.changeTop += 40;
            }
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        position: relative;

        .policeImg {
            position: absolute;
            right: 20px;
            bottom: 20px;
            z-index: 9;
            height: 250px;
        }

        .serverinfo-content {
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;

            .item {
                width: 100%;

                > p {
                    font-size: 2.4vh;
                    width: 100%;
                    margin-bottom: 1.2vh;
                    color: rgba(255, 255, 255, 1);
                    font-weight: 400;
                }

                .title {
                    font-weight: 600;
                    height: 6.6vh;
                    line-height: 6.6vh;
                    font-size: 2.6vh;
                }
            }

            .checked-info {
                margin-top: 3vh;
                font-size: 2.8vh;
                font-weight: 600;
                color: rgba(255, 255, 255, 1);
                line-height: 5.6vh;

                ::v-deep .el-checkbox {
                    display: flex;
                }

                ::v-deep .el-checkbox__inner {
                    width: 30px;
                    height: 30px;
                    background-color: #fff;

                }

                ::v-deep .el-checkbox__label {
                    //display:flex;
                    width: 3.0vw;
                    height: 3.4vh;
                    line-height: 3.0vh;
                    font-size: 2.8vh;
                    font-weight: 600;
                    color: white;
                }

                ::v-deep .is-checked .el-checkbox__inner::after {
                    border: 5px solid #3A84BF;
                    border-left: 0;
                    border-top: 0;
                    width: 4px;
                    height: 17px;
                    left: 11px;
                }
            }

        }

    }

    .disable-info {
        cursor: no-drop;
        background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%);
        border-image: linear-gradient(180deg, rgba(225, 235, 243, 1), rgba(214, 227, 239, 1)) 1 1;
        color: #fff;
    }

    .introduce-info {
        overflow: hidden;
        height: 31vh;
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        .text-info {
            height: 100%;
            width: 85%;
            position: relative;
            font-weight: 400;
            color: #fff;
            font-size: 26px;
            line-height: 40px;
        }

        .onePageTextInfo {
            width: 100%;
            font-size: 2vh;
        }

        .change-page {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        ::v-deep .el-loading-mask {
            background: none;
        }

        ::v-deep .el-loading-spinner .path {
            stroke: #ffffff;
        }

        ::v-deep .el-button {
            color: #ffffff;
            /*width: 100px;*/
            /*height: 60px;*/
            font-size: 22px;
            background: linear-gradient(180deg, rgba(137, 189, 242, 1) 0%, rgba(79, 153, 225, 1) 100%);
            border-radius: 8px;
            border: 1px solid;
            margin: 0 0 20px 0;

        }

        ::v-deep .el-button.is-disabled, .el-button.is-disabled:focus, .el-button.is-disabled:hover {
            cursor: auto;
        }

        ::v-deep .el-button.is-disabled {
            background: linear-gradient(#C5D7EA, #A5BFD8);
        }

    }

</style>
